body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
}

ul {
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.invisible {
  color: rgba(0, 0, 0, 0);
}

.backdrop.is-hidden {
  display: none;
}

.backdrop {
  width: 100%;
  height: 100vh;
  background-color: #696969;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

header {
  display: flex;
}

.menu {
  gap: 24px;
  display: flex;
}

.menu a {
  color: inherit;
  text-transform: uppercase;
  font-weight: bold;
}

.menu a.active {
  color: red;
}

#lang-select {
  margin-left: auto;
}

